

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import * as types from '@/store/mutations_type.js'
import App from './App'
import store from '@/store/'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'

Vue.use(AjaxPlugin)
Vue.use(VueRouter)
const routes = [
{
path: '*',
redirect: '/home/__all__'
},
{
path: '/HOME/:type',
component: function (resolve) {
require(['@/components/HelloFromVux'], resolve)
}
},
{
path: '/content/:id',
component: function (resolve) {
require(['@/components/Content'], resolve)
}
}
]

const router = new VueRouter({
mode:"history",
routes
})


FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
AjaxPlugin,
store,
router,
render: h => h(App)
}).$mount('#app-box')



