import Vue  from 'vue'
import Vuex from 'vuex'
import getter from './getter.js'
import mutations from './mutations.js'
import actions from './actions.js'
Vue.use(Vuex)
const state = {
     navList:[
        {name:'推荐',type:'__all__'},
        {name:'热点',type:'news_hot'},
        {name:'社会',type:'news_society'},
        {name:'娱乐',type:'news_entertainment'},
        {name:'科技',type:'news_tech'},
      ],
     ifReturnState: true,            // 是否返回更多新闻
     newsLength: 0,                  // 刷新返回的新闻数量
	 list: {                        // 新闻
        __all__: [],
        news_hot: [],
        news_society: [],
        news_tech: [],
        news_entertainment: [],
        news_car: [],
        news_sports: [],
        news_finance: [],
        news_military: [],
        news_world: [],
        news_fashion: []
    },
    article: {                      // 文章
        title: '',
        detail_source: '',
        content: ''
    },
    ifReturnRefresh: false,         // 是否刷新成功
    type:1,                   // 记录从主页离开时的新闻类型,postion位置                   
    positionY:0
}
export default  new Vuex.Store({
	state,
	mutations,
	actions,
})


